<script setup>
import { onMounted, ref, Transition } from 'vue';
import IconAddGroup from "../icons/IconAddGroup.vue";
import { useContatcsStore } from "@/stores/contatcs";

const contactsStore = useContatcsStore();

const name = ref("");
const phone = ref("");
const email = ref("");
const tag = ref(null);

const isGroupShow = ref(false);

const addContact = ref(null);

onMounted(() => {
    const btn = document.querySelector(".edit__content-btn")
    if (document.querySelectorAll(".modal-group__item")) {
        const modalGroupItem = document.querySelectorAll(".modal-group__item")
        for (let i = 0; i < modalGroupItem.length; i++) {
            modalGroupItem[i].addEventListener("click", function () {
                btn.disabled = false
                tag.value = this.innerHTML;
                addContact.value = {
                    id: contactsStore?.contactUsers?.length + 1,
                    tag,
                    name,
                    phone,
                    email,
                    detailPage: function () {
                        return this.id
                    }
                }
            })
        }
    }
})

</script>

<template>
    <div class="modal" @click="contactsStore.isShow = false">
        <div class="modal__content" @click.stop>
            <h2 class="modal__content-title">Add contact</h2>
            <form action="" class="modal__content-form" @submit.prevent="contactsStore.addContact(addContact)">
                <label class="edit__content-label">
                    Name
                    <input type="text" class="edit__content-inp" required v-model="name" placeholder="Name">
                </label>
                <label class="edit__content-label">
                    Phone
                    <input type="tel" class="edit__content-inp" required v-model="phone" placeholder="Phone">
                </label>
                <label class="edit__content-label">
                    Email
                    <input type="email" class="edit__content-inp" v-model="email" placeholder="Email">
                </label>
                <span class="group" @click="isGroupShow = true">
                    <IconAddGroup />
                </span>
                <ul class="modal-group" :class="{ active: isGroupShow }">
                    <li class="modal-group__item" data-type="Family" @click="isGroupShow = false">Family</li>
                    <li class="modal-group__item" data-type="My contacts" @click="isGroupShow = false">My contacts</li>
                    <li class="modal-group__item" data-type="Friends" @click="isGroupShow = false">Friends</li>
                    <li class="modal-group__item" data-type="Colleagues" @click="isGroupShow = false">Colleagues</li>
                </ul>
                <button class="edit__content-btn btn" disabled="true">Add</button>
            </form>
        </div>
    </div>
</template>

<style lang="scss" scoped></style>