<script setup>
import ContactsCard from "./ContactsCard.vue";
import IconSearch from "../icons/IconSearch.vue";
import { useContatcsStore } from "@/stores/contatcs";
import { computed, ref, TransitionGroup } from 'vue';

const contactsStore = useContatcsStore();
const search = ref("");

const filterContacts = computed(() => {
    return contactsStore.contactUsers.filter(c => {
        const searchVal = search.value.toLowerCase();
        return (
            c?.name.toLowerCase().includes(searchVal) ||
            c?.phone.includes(searchVal) ||
            c?.tag.toLowerCase().includes(searchVal)
        )
    })
})

</script>

<template>
    <section class="contacts">
        <div class="container">
            <h2 class="contacts__title title">Contacts</h2>
            <div class="contacts__content">
                <label class="contacts__content-label">
                    <IconSearch class="contacts__content-search" />
                    <input type="text" v-model="search" placeholder="Search contact" class="contacts__content-input">
                </label>
                <TransitionGroup name="list" class="contacts__content-cards" tag="div"
                    v-if="contactsStore.contactUsers.length">
                    <ContactsCard v-for="(item, idx) in filterContacts" :key="idx" :info="item" :id="idx" />
                </TransitionGroup>
                <p class="contacts__content-empty" v-else>No contacts</p>
            </div>
        </div>
    </section>
</template>

<style lang="scss" scoped></style>
