<script setup>
import { RouterLink } from 'vue-router'
import IconPhone from "../icons/IconPhone.vue";
import IconMail from "../icons/IconMail.vue";
import IconDel from "../icons/IconDel.vue";
import IconEdit from "../icons/IconEdit.vue";
import IconAva from "../icons/IconAva.vue";

import { useContatcsStore } from "@/stores/contatcs";

const contactsStore = useContatcsStore();

const props = defineProps({
    info: {
        type: Object,
        required: true
    },
    id: {
        type: Number,
        required: true
    }
})

</script>

<template>
    <div class="contacts__content-card list-move" :data-type="info.tag">
        <h3 class="contacts__content-title">
            {{ info.name }}
            <small>{{ info.tag ? `(${info.tag})` : "" }}</small>
            <div class="contacts__content-image">
                <img v-if="info.ava" :src="info.ava" alt="" class="contacts__content-ava">
                <IconAva widthAva="60" heightAva="60" v-else />
            </div>
        </h3>
        <a href="#!" class="contacts__content-tel">
            <IconPhone />
            {{ info.phone }}
        </a>
        <a href="#!" class="contacts__content-email">
            <IconMail /> {{ info.email }}
        </a>
        <div class="contacts__content-btns">
            <RouterLink :to="`/contacts/${id}`" class="contacts__content-edit">
                <IconEdit />
            </RouterLink>
            <span class="contacts__content-del" @click="contactsStore.removeContact(id)">
                <IconDel />
            </span>
        </div>
    </div>
</template>

<style></style>
